import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { FluidImageSharp } from 'src/models/image';
import GatsbyImage from 'gatsby-image';
import Emoji from '../Utils/Emoji';
import BenefitCard from '../Cards/BenefitCard';

const activities = [
  {
    title: '🎓 스터디',
    description: '',
  },
  {
    title: '🚀 멘토링',
    description: '',
  },
  {
    title: '🛰 커뮤니티',
    description: '',
  },
  {
    title: '💻 프로젝트',
    description: '',
  },
];

const useStyles = makeStyles(({ spacing, typography }) => ({
  root: {
    padding: `${spacing(15)}px 0`,
  },
  title: {
    fontWeight: typography.fontWeightBold,
  },
  description: {
    marginBottom: spacing(10),
  },
}));

const ActivitySection = () => {
  const styles = useStyles();
  const { thumbnail } = useStaticQuery<{ thumbnail: FluidImageSharp }>(graphql`
    query {
      thumbnail: file(absolutePath: { regex: "/teaching.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <section className={styles.root}>
      <Container>
        <Grid container spacing={10}>
          <Grid item xs={12} sm={5}>
            <GatsbyImage fluid={thumbnail.childImageSharp.fluid} />
          </Grid>
          <Grid item xs={12} sm={7}>
            <Typography variant="h4" component="h2" gutterBottom={true} className={styles.title}>
              <Emoji emoji="🤝" name="handshake" /> 함께 성장하고 공유하기
            </Typography>
            <Typography className={styles.description}>
              루비콘 팀의 아젠다는 <strong>성장과 공유</strong>입니다. 우리는 이 아젠다를 향해 달리기 위해 몇 가지
              활동을 진행하고 있어요.
            </Typography>
            <Grid container>
              {activities.map(({ title, description }, index) => (
                <Grid key={index} item xs={12} sm={3}>
                  <BenefitCard title={title} description={description} />
                </Grid>
              ))}
            </Grid>
          </Grid>
        </Grid>
      </Container>
    </section>
  );
};

export default ActivitySection;
