import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import { useBlogFeeds } from 'src/hooks';
import { convertBlogFeedToPost } from 'utils/posting';
import PostCard from 'components/Cards/PostCard';

const useStyles = makeStyles(({ palette, spacing, typography }) => ({
  root: {
    padding: `${spacing(15)}px 0`,
    backgroundColor: palette.grey['100'],
  },
  title: {
    fontWeight: typography.fontWeightBold,
  },
  description: {
    marginBottom: spacing(10),
  },
}));

const BlogSection = () => {
  const styles = useStyles();
  const blogs = useBlogFeeds({ limit: 12 });

  return (
    <section className={styles.root}>
      <Container>
        <Typography variant="h4" component="h2" gutterBottom={true} className={styles.title}>
          멤버들의 포스팅이에요
        </Typography>
        <Typography className={styles.description}>
          루비콘은 빠르고 방향성 있는 성장을 지향하고 있습니다.
          <br />
          루비콘 팀의 멤버들은 꾸준한 학습과 팀 내 공유로 이런 성장을 유지하려고 노력하고 있습니다.
        </Typography>
        <Grid container spacing={2}>
          {blogs.map(blog => (
            <Grid key={blog.link} item xs={12} sm={3}>
              <PostCard post={convertBlogFeedToPost(blog)} />
            </Grid>
          ))}
        </Grid>
      </Container>
    </section>
  );
};

export default BlogSection;
