import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import { useLubyconRepos } from 'src/hooks/useLubyconRepos';
import RepositoryCard from '../Cards/RepositoryCard';

const useStyles = makeStyles(({ spacing, typography }) => ({
  root: {
    padding: `${spacing(15)}px 0`,
  },
  title: {
    fontWeight: typography.fontWeightBold,
  },
  description: {
    marginBottom: spacing(10),
  },
}));

const RepositorySection = () => {
  const styles = useStyles();
  const repos = useLubyconRepos({ limit: 12 });

  if (repos.length === 0) {
    return null;
  }

  return (
    <section className={styles.root}>
      <Container>
        <Typography variant="h4" component="h2" gutterBottom={true} className={styles.title}>
          Projects
        </Typography>
        <Typography className={styles.description}>
          루비콘의 모든 프로젝트는 Github을 사용하여 진행되고 있고,
          <br />
          루비콘 멤버 뿐 아니라 멘토링 프로젝트에 참여한 멘티들도 함께 참여할 수 있는 프로젝트를 지속적으로 이어나가고
          있습니다
        </Typography>
        <Grid container spacing={2}>
          {repos.map(repo => (
            <Grid key={repo.title} item xs={12} sm={3}>
              <RepositoryCard repository={repo} />
            </Grid>
          ))}
        </Grid>
      </Container>
    </section>
  );
};

export default RepositorySection;
